"use client";

import { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Switch } from "@/components/ui/switch";
import { Label } from "@/components/ui/label";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { toast } from "sonner";
import { Toaster } from "@/components/ui/sonner";

// Types
interface Player {
  id: string;
  name: string;
  team: string;
  age?: number;
  season: string;
}

interface TrainingSession {
  id: string;
  date: string;
  time: string;
  title: string;
  location?: string;
  notes?: string;
}

interface AttendanceRecord {
  playerId: string;
  sessionId: string;
  status: 'present' | 'absent' | 'excused';
}

export default function SchedulePage() {
  const [players, setPlayers] = useState<Player[]>([]);
  const [draftSessions, setDraftSessions] = useState<TrainingSession[]>([]);
  const [liveSessions, setLiveSessions] = useState<TrainingSession[]>([]);
  const [attendance, setAttendance] = useState<AttendanceRecord[]>([]);
  const [editingEnabled, setEditingEnabled] = useState(false);
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState("draft");

  // Load initial data from your Google Sheets structure
  useEffect(() => {
    loadInitialData();
  }, []);

  const loadInitialData = () => {
    // Sample data based on your Google Sheets structure
    const samplePlayers: Player[] = [
      { id: "1", name: "Kaden Hockenberry", team: "07B Black", season: "2024/2025" },
      { id: "2", name: "Landen Mayer", team: "07B Red", season: "2024/2025" },
      { id: "3", name: "Wiktor Mroczkowski", team: "08/09 B Black", season: "2024/2025" },
      { id: "4", name: "Jake Bernier", team: "08/09 B Black", season: "2024/2025" },
      { id: "5", name: "Sawyer Patchett", team: "08/09 B Red", season: "2024/2025" },
      { id: "6", name: "Ben Kougher", team: "08/09 B Red", season: "2024/2025" },
      { id: "7", name: "Tristan Kilko", team: "10B Black", season: "2024/2025" },
      { id: "8", name: "Sophia Morales", team: "10G Black", season: "2024/2025" },
      { id: "9", name: "Kyla Harper", team: "10G Red", season: "2024/2025" },
      { id: "10", name: "Lian Weibley", team: "11B Black", season: "2024/2025" },
      { id: "11", name: "Emily Bowers", team: "11G Black", season: "2024/2025" },
    ];

    const sampleDraftSessions: TrainingSession[] = [
      { id: "1", date: "2024-11-15", time: "18:00", title: "Fall GK Training - Week 1", location: "Field A" },
      { id: "2", date: "2024-11-22", time: "18:00", title: "Fall GK Training - Week 2", location: "Field A" },
      { id: "3", date: "2024-11-29", time: "18:00", title: "Fall GK Training - Week 3", location: "Field A" },
      { id: "4", date: "2024-12-06", time: "18:00", title: "Fall GK Training - Week 4", location: "Field A" },
      { id: "5", date: "2024-12-13", time: "18:00", title: "Fall GK Training - Week 5", location: "Field A" },
    ];

    const sampleLiveSessions: TrainingSession[] = [
      { id: "1", date: "2024-11-15", time: "18:00", title: "Fall GK Training - Week 1", location: "Field A" },
      { id: "2", date: "2024-11-22", time: "18:00", title: "Fall GK Training - Week 2", location: "Field A" },
    ];

    setPlayers(samplePlayers);
    setDraftSessions(sampleDraftSessions);
    setLiveSessions(sampleLiveSessions);
    setLoading(false);
    toast.success("Schedule data loaded successfully");
  };

  const copyDraftToLive = () => {
    setLiveSessions([...draftSessions]);
    toast.success("Draft schedule copied to live schedule");
  };

  const updatePlayer = (playerId: string, field: keyof Player, value: string) => {
    setPlayers(prev => prev.map(player => 
      player.id === playerId ? { ...player, [field]: value } : player
    ));
    toast.success("Player updated");
  };

  const updateSession = (sessionId: string, field: keyof TrainingSession, value: string) => {
    if (activeTab === "draft") {
      setDraftSessions(prev => prev.map(session => 
        session.id === sessionId ? { ...session, [field]: value } : session
      ));
    } else {
      setLiveSessions(prev => prev.map(session => 
        session.id === sessionId ? { ...session, [field]: value } : session
      ));
    }
    toast.success("Session updated");
  };

  const addNewSession = () => {
    const newSession: TrainingSession = {
      id: Date.now().toString(),
      date: "",
      time: "",
      title: "New Training Session",
      location: "Field A"
    };
    
    if (activeTab === "draft") {
      setDraftSessions(prev => [...prev, newSession]);
    } else {
      setLiveSessions(prev => [...prev, newSession]);
    }
    toast.success("New session added");
  };

  const addNewPlayer = () => {
    const newPlayer: Player = {
      id: Date.now().toString(),
      name: "New Player",
      team: "Team Name",
      season: "2024/2025"
    };
    
    setPlayers(prev => [...prev, newPlayer]);
    toast.success("New player added");
  };

  const toggleAttendance = (playerId: string, sessionId: string) => {
    setAttendance(prev => {
      const existing = prev.find(a => a.playerId === playerId && a.sessionId === sessionId);
      if (existing) {
        const newStatus = existing.status === 'present' ? 'absent' : 
                         existing.status === 'absent' ? 'excused' : 'present';
        return prev.map(a => 
          a.playerId === playerId && a.sessionId === sessionId 
            ? { ...a, status: newStatus }
            : a
        );
      } else {
        return [...prev, { playerId, sessionId, status: 'present' as const }];
      }
    });
  };

  const getAttendanceStatus = (playerId: string, sessionId: string) => {
    const record = attendance.find(a => a.playerId === playerId && a.sessionId === sessionId);
    return record?.status || 'absent';
  };

  const currentSessions = activeTab === "draft" ? draftSessions : liveSessions;

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-gray-900 mx-auto mb-4"></div>
          <p className="text-gray-600">Loading schedule data...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-red-50 to-gray-100">
      <Toaster />
      
      {/* Header */}
      <div className="bg-gradient-to-r from-red-600 to-gray-900 shadow-lg border-b-4 border-red-700">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-6">
            <div className="flex items-center space-x-4">
              <img 
                src="/eagle-fc-logo.svg" 
                alt="Eagle FC Logo" 
                className="h-16 w-16"
                onError={(e) => { e.currentTarget.style.display = 'none'; }}
              />
              <div>
                <h1 className="text-3xl font-bold text-white">Eagle FC</h1>
                <p className="text-red-100">Goalkeeper Training Schedule</p>
              </div>
            </div>
            <div className="flex items-center space-x-4">
              <div className="flex items-center space-x-2">
                <Switch
                  id="edit-mode"
                  checked={editingEnabled}
                  onCheckedChange={setEditingEnabled}
                />
                <Label htmlFor="edit-mode" className="text-sm font-medium text-white">
                  {editingEnabled ? "Editing Enabled" : "View Only"}
                </Label>
              </div>
              <Badge variant={editingEnabled ? "destructive" : "secondary"} className={editingEnabled ? "bg-red-700 text-white" : "bg-gray-200 text-gray-800"}>
                {editingEnabled ? "Coach Mode" : "View Mode"}
              </Badge>
            </div>
          </div>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="grid grid-cols-1 lg:grid-cols-4 gap-8">
          {/* Players Panel */}
          <div className="lg:col-span-1">
            <Card className="border-red-200 shadow-lg">
              <CardHeader className="bg-gradient-to-r from-red-600 to-gray-800">
                <div className="flex justify-between items-center">
                  <CardTitle className="text-white">Players ({players.length})</CardTitle>
                  {editingEnabled && (
                    <Button 
                      onClick={addNewPlayer} 
                      size="sm" 
                      className="bg-red-700 hover:bg-red-800 text-white"
                    >
                      Add Player
                    </Button>
                  )}
                </div>
              </CardHeader>
              <CardContent className="space-y-3 bg-white">
                {players.map((player) => (
                  <div key={player.id} className="p-3 bg-gradient-to-r from-red-50 to-gray-50 rounded-lg border border-red-100">
                    {editingEnabled ? (
                      <div className="space-y-2">
                        <Input
                          value={player.name}
                          onChange={(e) => updatePlayer(player.id, 'name', e.target.value)}
                          className="text-sm font-medium border-red-200 focus:border-red-500"
                        />
                        <Input
                          value={player.team}
                          onChange={(e) => updatePlayer(player.id, 'team', e.target.value)}
                          className="text-xs border-red-200 focus:border-red-500"
                        />
                      </div>
                    ) : (
                      <div>
                        <p className="text-sm font-medium text-gray-900">{player.name}</p>
                        <p className="text-xs text-red-600 font-medium">{player.team}</p>
                      </div>
                    )}
                  </div>
                ))}
              </CardContent>
            </Card>
          </div>

          {/* Schedule Panel */}
          <div className="lg:col-span-3">
            <Card className="border-red-200 shadow-lg">
              <CardHeader className="bg-gradient-to-r from-red-600 to-gray-800">
                <div className="flex justify-between items-center">
                  <CardTitle className="text-white">Training Schedule</CardTitle>
                  <div className="flex space-x-2">
                    {editingEnabled && (
                      <>
                        <Button onClick={addNewSession} variant="outline" size="sm" className="bg-white text-red-600 border-red-200 hover:bg-red-50">
                          Add Session
                        </Button>
                        <Button onClick={copyDraftToLive} size="sm" className="bg-red-700 hover:bg-red-800">
                          Copy Draft to Live
                        </Button>
                      </>
                    )}
                  </div>
                </div>
              </CardHeader>
              <CardContent className="bg-white">
                <Tabs value={activeTab} onValueChange={setActiveTab}>
                  <TabsList className="grid w-full grid-cols-2">
                    <TabsTrigger value="draft" className="data-[state=active]:bg-red-600 data-[state=active]:text-white">Draft Schedule</TabsTrigger>
                    <TabsTrigger value="live" className="data-[state=active]:bg-red-600 data-[state=active]:text-white">Live Schedule</TabsTrigger>
                  </TabsList>
                  
                  <TabsContent value="draft" className="mt-6">
                    <div className="space-y-4">
                      {draftSessions.map((session) => (
                        <SessionCard
                          key={session.id}
                          session={session}
                          players={players}
                          editingEnabled={editingEnabled}
                          onUpdateSession={updateSession}
                          onToggleAttendance={toggleAttendance}
                          getAttendanceStatus={getAttendanceStatus}
                        />
                      ))}
                    </div>
                  </TabsContent>
                  
                  <TabsContent value="live" className="mt-6">
                    <div className="space-y-4">
                      {liveSessions.map((session) => (
                        <SessionCard
                          key={session.id}
                          session={session}
                          players={players}
                          editingEnabled={editingEnabled}
                          onUpdateSession={updateSession}
                          onToggleAttendance={toggleAttendance}
                          getAttendanceStatus={getAttendanceStatus}
                        />
                      ))}
                    </div>
                  </TabsContent>
                </Tabs>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    </div>
  );
}

// Session Card Component
interface SessionCardProps {
  session: TrainingSession;
  players: Player[];
  editingEnabled: boolean;
  onUpdateSession: (sessionId: string, field: keyof TrainingSession, value: string) => void;
  onToggleAttendance: (playerId: string, sessionId: string) => void;
  getAttendanceStatus: (playerId: string, sessionId: string) => string;
}

function SessionCard({ 
  session, 
  players, 
  editingEnabled, 
  onUpdateSession, 
  onToggleAttendance, 
  getAttendanceStatus 
}: SessionCardProps) {
  return (
    <Card className="border-red-100 shadow-md">
      <CardHeader className="bg-gradient-to-r from-red-50 to-gray-50">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <div>
            <Label className="text-xs text-gray-500">Title</Label>
            {editingEnabled ? (
              <Input
                value={session.title}
                onChange={(e) => onUpdateSession(session.id, 'title', e.target.value)}
                className="mt-1 border-red-200 focus:border-red-500"
              />
            ) : (
              <p className="font-medium text-gray-900">{session.title}</p>
            )}
          </div>
          <div>
            <Label className="text-xs text-gray-500">Date</Label>
            {editingEnabled ? (
              <Input
                type="date"
                value={session.date}
                onChange={(e) => onUpdateSession(session.id, 'date', e.target.value)}
                className="mt-1 border-red-200 focus:border-red-500"
              />
            ) : (
              <p className="font-medium text-gray-900">{session.date}</p>
            )}
          </div>
          <div>
            <Label className="text-xs text-gray-500">Time</Label>
            {editingEnabled ? (
              <Input
                type="time"
                value={session.time}
                onChange={(e) => onUpdateSession(session.id, 'time', e.target.value)}
                className="mt-1 border-red-200 focus:border-red-500"
              />
            ) : (
              <p className="font-medium text-gray-900">{session.time}</p>
            )}
          </div>
        </div>
      </CardHeader>
      <CardContent className="bg-white">
        <div className="space-y-3">
          <Label className="text-sm font-medium text-gray-700">Player Attendance</Label>
          <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-2">
            {players.map((player) => {
              const status = getAttendanceStatus(player.id, session.id);
              return (
                <button
                  key={player.id}
                  onClick={() => onToggleAttendance(player.id, session.id)}
                  className={`p-2 rounded-lg text-xs font-medium transition-colors ${
                    status === 'present' 
                      ? 'bg-green-100 text-green-800 border-green-200' 
                      : status === 'excused'
                      ? 'bg-yellow-100 text-yellow-800 border-yellow-200'
                      : 'bg-red-100 text-red-600 border-red-200'
                  } border hover:shadow-md`}
                >
                  {player.name}
                  <div className="text-xs mt-1">
                    {status === 'present' ? '✓' : status === 'excused' ? '!' : '✗'}
                  </div>
                </button>
              );
            })}
          </div>
        </div>
      </CardContent>
    </Card>
  );
}
